import request from '@/utils/request'

// 
export function listMaterial(query) {
  return request({
    url: '/buss/workshop/material/list',
    method: 'get',
    params: query
  })
}

// 
export function getMaterial(id) {
  return request({
    url: '/buss/workshop/material/' + id,
    method: 'get'
  })
}

// 
export function addMaterial(data) {
  return request({
    url: '/buss/workshop/material',
    method: 'post',
    data: data
  })
}

// 
export function updateMaterial(data) {
  return request({
    url: '/buss/workshop/material',
    method: 'put',
    data: data
  })
}

// 
export function delMaterial(id) {
  return request({
    url: '/buss/workshop/material/' + id,
    method: 'delete'
  })
}

// 
export function exportMaterial(query) {
  return request({
    url: '/buss/workshop/material/export',
    method: 'get',
    params: query
  })
}