import request from '@/utils/request'

// 
export function listGroup(query) {
  return request({
    url: '/buss/safe/group/list',
    method: 'get',
    params: query
  })
}

// 
export function getGroup(id) {
  return request({
    url: '/buss/safe/group/' + id,
    method: 'get'
  })
}

// 
export function addGroup(data) {
  return request({
    url: '/buss/safe/group',
    method: 'post',
    data: data
  })
}

// 
export function updateGroup(data) {
  return request({
    url: '/buss/safe/group',
    method: 'put',
    data: data
  })
}

// 
export function delGroup(id) {
  return request({
    url: '/buss/safe/group/' + id,
    method: 'delete'
  })
}

// 
export function exportGroup(query) {
  return request({
    url: '/buss/safe/group/export',
    method: 'get',
    params: query
  })
}