<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="" prop="ceName">
        <el-input
          v-model="queryParams.ceName"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="ceDate">
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.ceDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
        >
        </el-date-picker
        >-
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.ceDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          ></el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['buss:emergency:event:add']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['buss:emergency:event:edit']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['buss:emergency:event:remove']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          size="mini"
          :disabled="single"
          @click="handleExport"
          ></el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="eventList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="ceName" />
      <el-table-column label="" align="center" prop="ceType" />
      <el-table-column
        label=""
        align="center"
        prop="ceDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.ceDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="ceImages" />
      <el-table-column label="" align="center" prop="ceAddress" />
      <el-table-column label="" align="center" prop="ceContent" />
      <el-table-column label="" align="center" prop="ceUploadUser" />
      <el-table-column
        label=""
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            ></el-button>
            <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleExport(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="ceName">
              <el-input v-model="form.ceName" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="ceType">
              <el-select
                v-model="form.ceType"
                placeholder=""
                style="width: 250px"
              >
                <el-option label="" value="" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="ceDate">
              <el-date-picker
                clearable
                size="small"
                style="width: 250px"
                v-model="form.ceDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="ceUploadUser">
              <el-input
                v-model="form.ceUploadUser"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="" prop="ceAddress">
              <el-input v-model="form.ceAddress" placeholder="" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="" prop="ceImages">
              <el-input v-model="form.ceImages" placeholder="" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="">
              <el-input
                type="textarea"
                v-model="form.ceContent"
                :autosize="{ minRows: 5, maxRows: 10 }"
                maxlength="2000"
                show-word-limit
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog :title="title" :visible.sync="add" width="500px" append-to-body>
      <el-form ref="form" :model="addform" :rules="rules" label-width="80px">
        <el-form-item label="" prop="ctDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="addform.ctDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
           <el-input type="textarea" v-model="addform.ctContent"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="trackSubmit"> </el-button>
        <el-button @click="add=false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listEvent,
  getEvent,
  delEvent,
  addEvent,
  updateEvent,
  exportEvent,
} from "@/api/buss/emergency/event";
import {
  addTrack
} from "@/api/buss/emergency/track";



export default {
  name: "Event",
  components: {
  },
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      eventList: [],
      // 
      title: "",
      // 
      open: false,
      add:false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        ceName: null,
        ceType: null,
        ceDate: null,
        ceAddress: null,
        ceImages: null,
        ceContent: null,
        ceUploadUser: null,
      },
      // 
      form: {},
      addform:{
        sysMainId:null,
        ctType:1,
      },
      // 
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listEvent(this.queryParams).then((response) => {
        this.eventList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        ceName: null,
        ceType: null,
        ceDate: null,
        ceAddress: null,
        ceImages: null,
        ceContent: null,
        ceUploadUser: null,
        id: null,
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getEvent(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    trackSubmit(){
      debugger
      if (this.addform.sysMainId != null) {
        addTrack(this.addform).then((response) => {
          this.msgSuccess("");
          this.add = false;
          this.getList();
        });
      }
    },
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateEvent(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addEvent(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '"' + ids + '"?',
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(function () {
          return delEvent(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("");
        });
    },
    /**  */
    handleExport(row) {
      debugger
      this.add=true;
      this.title = "";
      this.addform.sysMainId=row.id
    },
  },
};
</script>
