<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="" prop="sysHouseId">
        <el-input
          v-model="queryParams.sysHouseId"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          ></el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:electricity:add']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:electricity:edit']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:electricity:remove']"
          ></el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="electricityList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column
        label=""
        align="center"
        prop="elDegreesCurrrent"
      />
      <el-table-column label="" align="center" prop="elDegreesPre" />
      <el-table-column
        label=""
        align="center"
        prop="elDegreesDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elDegreesDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="elDegreesSum" />
      <el-table-column label="" align="center" prop="elDegreesPrice" />
      <el-table-column label="" align="center" prop="elPayShould" />
      <el-table-column label="" align="center" prop="elPayReality" />
      <el-table-column
        label=""
        align="center"
        prop="elPayDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elPayDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="elPayType" />
      <el-table-column
        label=""
        align="center"
        prop="elContractEnd"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elContractEnd, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        align="center"
        class-name="small-padding fixed-width"
        width="200px"
        fixed="right"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:electricity:edit']"
            ></el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:electricity:remove']"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elDegreesPre">
              <el-input
                v-model="form.elDegreesPre"
                placeholder=""
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elDegreesCurrrent">
              <el-input
                v-model="form.elDegreesCurrrent"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elDegreesDate">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.elDegreesDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elDegreesSum">
              <el-input
                v-model="form.elDegreesSum"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12"> 
            <el-form-item label="" prop="elDegreesPrice">
              <el-input
                v-model="form.elDegreesPrice"
                placeholder=""
              />
            </el-form-item>
        </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elPayShould">
              <el-input
                v-model="form.elPayShould"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
          <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elPayReality">
              <el-input
                v-model="form.elPayReality"
                placeholder=""
              />
            </el-form-item>
        </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elPayDate">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.elPayDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
            </el-col>
        </el-row>
          <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elPayType">
              <el-select v-model="form.elPayType" placeholder="" style="width:100%">
                <el-option label="" value="" />
              </el-select>
            </el-form-item>
        </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elContractEnd">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.elContractEnd"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listElectricity,
  getElectricity,
  delElectricity,
  addElectricity,
  updateElectricity,
  exportElectricity,
} from "@/api/buss/house/electricity";

export default {
  name: "Electricity",
  components: {},
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      electricityList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysHouseId: null,
        sysLeaseId: null,
        elDegreesCurrrent: null,
        elDegreesPre: null,
        elDegreesDate: null,
        elDegreesSum: null,
        elDegreesPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      },
      // 
      form: {},
      // 
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listElectricity(this.queryParams).then((response) => {
        this.electricityList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        sysHouseId: null,
        sysLeaseId: null,
        elDegreesCurrrent: null,
        elDegreesPre: null,
        elDegreesDate: null,
        elDegreesSum: null,
        elDegreesPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getElectricity(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateElectricity(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addElectricity(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '"' + ids + '"?',
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(function () {
          return delElectricity(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("");
        });
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(function () {
          return exportElectricity(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
