<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="" prop="paName">
        <el-input
          v-model="queryParams.paName"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="paType">
        <el-select v-model="queryParams.paType" placeholder="" clearable size="small">
             <el-option
            v-for="dict in bindData.paType"
            :key="dict.dictValue"
            :label="dict.dictLabel"
            :value="dict.dictValue"
          />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"></el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:paper:add']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:paper:edit']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:paper:remove']"
        ></el-button>
      </el-col>
    </el-row>
    <el-table v-loading="loading" :data="paperList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="paName" />
      <el-table-column label="" align="center" prop="paCode" />
      <el-table-column label="" align="center" prop="paType">
        <template slot-scope="scope">
          <span>{{ getDictValue(scope.row.paType)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="paUploadTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.paUploadTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="paFilePath" />
      <el-table-column label="" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:paper:edit']"
          ></el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:paper:remove']"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="" prop="paName">
          <el-input v-model="form.paName" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="paCode">
          <el-input v-model="form.paCode" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="paType">
          <el-select v-model="form.paType" placeholder="">
             <el-option
                v-for="dict in bindData.paType"
                :key="dict.dictValue"
                :label="dict.dictLabel"
                :value="dict.dictValue"
              />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="paUploadTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.paUploadTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="paFilePath">
          <el-input v-model="form.paFilePath" placeholder="" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listPaper, getPaper, delPaper, addPaper, updatePaper, exportPaper } from "@/api/buss/base/paper";
import { dictInit,dictCustom,getDictValue } from "@/api/buss/base/cache";

export default {
  name: "Paper",
  components: {
  },
  data() {
    return {
      bindData:{
        paType:[]
      },
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      paperList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        paName: null,
        paCode: null,
        paType: null,
        paUploadTime: null,
        paFilePath: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      },
      // 
      form: {},
      // 
      rules: {

      }
    };
  },
  created() {
    this.dictInit();
    this.getList();
  },
  methods: {
    async dictInit(){
      await dictInit("paper_type");
      this.bindData.paType=dictCustom("paper_type");
    }, 
    getDictValue(cdCode){
      return getDictValue(cdCode,this.bindData.paType);
    },
    /**  */
    getList() {
      this.loading = true;
      listPaper(this.queryParams).then(response => {
        this.paperList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        paName: null,
        paCode: null,
        paType: null,
        paUploadTime: null,
        paFilePath: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getPaper(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updatePaper(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addPaper(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('"' + ids + '"?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return delPaper(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("");
        })
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return exportPaper(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
