import request from '@/utils/request'

// 
export function listRepair(query) {
  return request({
    url: '/buss/device/repair/list',
    method: 'get',
    params: query
  })
}

// 
export function getRepair(id) {
  return request({
    url: '/buss/device/repair/' + id,
    method: 'get'
  })
}

// 
export function addRepair(data) {
  return request({
    url: '/buss/device/repair',
    method: 'post',
    data: data
  })
}

// 
export function updateRepair(data) {
  return request({
    url: '/buss/device/repair',
    method: 'put',
    data: data
  })
}

// 
export function delRepair(id) {
  return request({
    url: '/buss/device/repair/' + id,
    method: 'delete'
  })
}

// 
export function exportRepair(query) {
  return request({
    url: '/buss/device/repair/export',
    method: 'get',
    params: query
  })
}