<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="id" prop="sysWorkshopId">
        <el-input
          v-model="queryParams.sysWorkshopId"
          placeholder="id"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="cwName">
        <el-input
          v-model="queryParams.cwName"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="cwSex">
        <el-select v-model="queryParams.cwSex" placeholder="" clearable size="small">
          <el-option label="" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="cwBirthday">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.cwBirthday"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="cwDuty">
        <el-input
          v-model="queryParams.cwDuty"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="cwTell">
        <el-input
          v-model="queryParams.cwTell"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="cwPhoto">
        <el-input
          v-model="queryParams.cwPhoto"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="cwContract">
        <el-input
          v-model="queryParams.cwContract"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="cwDutyCertificate">
        <el-input
          v-model="queryParams.cwDutyCertificate"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="cwJoinDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.cwJoinDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"></el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:works:add']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:works:edit']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:works:remove']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:works:export']"
        ></el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="worksList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="id" />
      <el-table-column label="id" align="center" prop="sysWorkshopId" />
      <el-table-column label="" align="center" prop="cwName" />
      <el-table-column label="" align="center" prop="cwSex" />
      <el-table-column label="" align="center" prop="cwBirthday" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.cwBirthday, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="cwDuty" />
      <el-table-column label="" align="center" prop="cwTell" />
      <el-table-column label="" align="center" prop="cwPhoto" />
      <el-table-column label="" align="center" prop="cwContract" />
      <el-table-column label="" align="center" prop="cwDutyCertificate" />
      <el-table-column label="" align="center" prop="cwJoinDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.cwJoinDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:works:edit']"
          ></el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:works:remove']"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="id" prop="sysWorkshopId">
          <el-input v-model="form.sysWorkshopId" placeholder="id" />
        </el-form-item>
        <el-form-item label="" prop="cwName">
          <el-input v-model="form.cwName" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="cwSex">
          <el-select v-model="form.cwSex" placeholder="">
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="cwBirthday">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.cwBirthday"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="cwDuty">
          <el-input v-model="form.cwDuty" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="cwTell">
          <el-input v-model="form.cwTell" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="cwPhoto">
          <el-input v-model="form.cwPhoto" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="cwContract">
          <el-input v-model="form.cwContract" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="cwDutyCertificate">
          <el-input v-model="form.cwDutyCertificate" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="cwJoinDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.cwJoinDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listWorks, getWorks, delWorks, addWorks, updateWorks, exportWorks } from "@/api/buss/workshop/works";

export default {
  name: "Works",
  components: {
  },
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      worksList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysWorkshopId: null,
        cwName: null,
        cwSex: null,
        cwBirthday: null,
        cwDuty: null,
        cwTell: null,
        cwPhoto: null,
        cwContract: null,
        cwDutyCertificate: null,
        cwJoinDate: null
      },
      // 
      form: {},
      // 
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listWorks(this.queryParams).then(response => {
        this.worksList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        sysWorkshopId: null,
        cwName: null,
        cwSex: null,
        cwBirthday: null,
        cwDuty: null,
        cwTell: null,
        cwPhoto: null,
        cwContract: null,
        cwDutyCertificate: null,
        cwJoinDate: null
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getWorks(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateWorks(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addWorks(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('"' + ids + '"?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return delWorks(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("");
        })
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return exportWorks(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
