<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="" prop="sysWorkshopId">
        <el-input
          v-model="queryParams.sysWorkshopId"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="sysLeaseId">
        <el-input
          v-model="queryParams.sysLeaseId"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="rlStartDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlStartDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="rlEndDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlEndDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="rlSquareMetrePrice">
        <el-input
          v-model="queryParams.rlSquareMetrePrice"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="rlPayShould">
        <el-input
          v-model="queryParams.rlPayShould"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="rlPayReality">
        <el-input
          v-model="queryParams.rlPayReality"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="rlPayDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlPayDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="rlPayType">
        <el-select v-model="queryParams.rlPayType" placeholder="" clearable size="small">
          <el-option label="" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="rlContractEnd">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlContractEnd"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"></el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:realty:add']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:realty:edit']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:realty:remove']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:realty:export']"
        ></el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="realtyList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="id" />
      <el-table-column label="" align="center" prop="sysWorkshopId" />
      <el-table-column label="" align="center" prop="sysLeaseId" />
      <el-table-column label="" align="center" prop="rlStartDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlStartDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="rlEndDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlEndDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="rlSquareMetrePrice" />
      <el-table-column label="" align="center" prop="rlPayShould" />
      <el-table-column label="" align="center" prop="rlPayReality" />
      <el-table-column label="" align="center" prop="rlPayDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlPayDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="rlPayType" />
      <el-table-column label="" align="center" prop="rlContractEnd" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlContractEnd, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:realty:edit']"
          ></el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:realty:remove']"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="" prop="sysWorkshopId">
          <el-input v-model="form.sysWorkshopId" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="sysLeaseId">
          <el-input v-model="form.sysLeaseId" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="rlStartDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlStartDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="rlEndDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlEndDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="rlSquareMetrePrice">
          <el-input v-model="form.rlSquareMetrePrice" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="rlPayShould">
          <el-input v-model="form.rlPayShould" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="rlPayReality">
          <el-input v-model="form.rlPayReality" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="rlPayDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlPayDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="rlPayType">
          <el-select v-model="form.rlPayType" placeholder="">
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="rlContractEnd">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlContractEnd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listRealty, getRealty, delRealty, addRealty, updateRealty, exportRealty } from "@/api/buss/workshop/realty";

export default {
  name: "Realty",
  components: {
  },
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      realtyList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysWorkshopId: null,
        sysLeaseId: null,
        rlStartDate: null,
        rlEndDate: null,
        rlSquareMetrePrice: null,
        rlPayShould: null,
        rlPayReality: null,
        rlPayDate: null,
        rlPayType: null,
        rlContractEnd: null
      },
      // 
      form: {},
      // 
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listRealty(this.queryParams).then(response => {
        this.realtyList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        sysWorkshopId: null,
        sysLeaseId: null,
        rlStartDate: null,
        rlEndDate: null,
        rlSquareMetrePrice: null,
        rlPayShould: null,
        rlPayReality: null,
        rlPayDate: null,
        rlPayType: null,
        rlContractEnd: null
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getRealty(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateRealty(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addRealty(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('"' + ids + '"?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return delRealty(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("");
        })
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return exportRealty(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
