<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="" prop="xcDeviceType">
        <el-select v-model="queryParams.xcDeviceType" placeholder="" clearable size="small">
          <el-option label="" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="xcDeviceName">
        <el-input
          v-model="queryParams.xcDeviceName"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="xcDeviceNorms">
        <el-input
          v-model="queryParams.xcDeviceNorms"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="xcDeviceCode">
        <el-input
          v-model="queryParams.xcDeviceCode"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="xcDevicePosition">
        <el-input
          v-model="queryParams.xcDevicePosition"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="xcDeviceStatus">
        <el-select v-model="queryParams.xcDeviceStatus" placeholder="" clearable size="small">
          <el-option label="" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="xcFaultDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.xcFaultDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="xcRepairName">
        <el-input
          v-model="queryParams.xcRepairName"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="xcRepairDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.xcRepairDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="sysCreateTime">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.sysCreateTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="sysCreatorId">
        <el-input
          v-model="queryParams.sysCreatorId"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="sysModifiedId">
        <el-input
          v-model="queryParams.sysModifiedId"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="sysModifiedTime">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.sysModifiedTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"></el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:repair:add']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:repair:edit']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:repair:remove']"
        ></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:repair:export']"
        ></el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="repairList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="id" />
      <el-table-column label="" align="center" prop="xcDeviceType" />
      <el-table-column label="" align="center" prop="xcDeviceName" />
      <el-table-column label="" align="center" prop="xcDeviceNorms" />
      <el-table-column label="" align="center" prop="xcDeviceCode" />
      <el-table-column label="" align="center" prop="xcDevicePosition" />
      <el-table-column label="" align="center" prop="xcDeviceStatus" />
      <el-table-column label="" align="center" prop="xcFaultDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.xcFaultDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="xcRepairName" />
      <el-table-column label="" align="center" prop="xcRepairContent" />
      <el-table-column label="" align="center" prop="xcRepairDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.xcRepairDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="sysCreateTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.sysCreateTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="sysCreatorId" />
      <el-table-column label="" align="center" prop="sysModifiedId" />
      <el-table-column label="" align="center" prop="sysModifiedTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.sysModifiedTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:repair:edit']"
          ></el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:repair:remove']"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="" prop="xcDeviceType">
          <el-select v-model="form.xcDeviceType" placeholder="">
            <el-option label="" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="xcDeviceName">
          <el-input v-model="form.xcDeviceName" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="xcDeviceNorms">
          <el-input v-model="form.xcDeviceNorms" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="xcDeviceCode">
          <el-input v-model="form.xcDeviceCode" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="xcDevicePosition">
          <el-input v-model="form.xcDevicePosition" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.xcDeviceStatus">
            <el-radio label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="xcFaultDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.xcFaultDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="xcRepairName">
          <el-input v-model="form.xcRepairName" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <editor v-model="form.xcRepairContent" :min-height="192"/>
        </el-form-item>
        <el-form-item label="" prop="xcRepairDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.xcRepairDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="sysCreateTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.sysCreateTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="sysCreatorId">
          <el-input v-model="form.sysCreatorId" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="sysModifiedId">
          <el-input v-model="form.sysModifiedId" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="sysModifiedTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.sysModifiedTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listRepair, getRepair, delRepair, addRepair, updateRepair, exportRepair } from "@/api/buss/device/repair";
import Editor from '@/components/Editor';

export default {
  name: "Repair",
  components: {
    Editor,
  },
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      repairList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        xcDeviceType: null,
        xcDeviceName: null,
        xcDeviceNorms: null,
        xcDeviceCode: null,
        xcDevicePosition: null,
        xcDeviceStatus: null,
        xcFaultDate: null,
        xcRepairName: null,
        xcRepairContent: null,
        xcRepairDate: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      },
      // 
      form: {},
      // 
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listRepair(this.queryParams).then(response => {
        this.repairList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        xcDeviceType: null,
        xcDeviceName: null,
        xcDeviceNorms: null,
        xcDeviceCode: null,
        xcDevicePosition: null,
        xcDeviceStatus: 0,
        xcFaultDate: null,
        xcRepairName: null,
        xcRepairContent: null,
        xcRepairDate: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getRepair(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateRepair(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addRepair(this.form).then(response => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('"' + ids + '"?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return delRepair(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("");
        })
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('?', "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(function() {
          return exportRepair(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
