import request from '@/utils/request'

// 
export function listOther(query) {
  return request({
    url: '/buss/workshop/other/list',
    method: 'get',
    params: query
  })
}

// 
export function getOther(id) {
  return request({
    url: '/buss/workshop/other/' + id,
    method: 'get'
  })
}

// 
export function addOther(data) {
  return request({
    url: '/buss/workshop/other',
    method: 'post',
    data: data
  })
}

// 
export function updateOther(data) {
  return request({
    url: '/buss/workshop/other',
    method: 'put',
    data: data
  })
}

// 
export function delOther(id) {
  return request({
    url: '/buss/workshop/other/' + id,
    method: 'delete'
  })
}

// 
export function exportOther(query) {
  return request({
    url: '/buss/workshop/other/export',
    method: 'get',
    params: query
  })
}