<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="" prop="lelContractEnd">
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.lelContractStart"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
        >
        </el-date-picker
        >-
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.lelContractEnd"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          ></el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:lease:add']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:lease:edit']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          ></el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:green"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          ></el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:yellow"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          ></el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:purple"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          ></el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:brown"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          ></el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="leaseList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="leLeaseUser" />
      <el-table-column label="" align="center" prop="leBussLicense" />
      <el-table-column label="" align="center" prop="leLegalPerson" />
      <el-table-column label="" align="center" prop="leLegalTell" />
      <el-table-column
        label=""
        align="center"
        prop="leCheckIndate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.leCheckIndate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        align="center"
        prop="leContractNearby"
      />
      <el-table-column label="" align="center" prop="leLeaseMonth" />
      <el-table-column label="" align="center" prop="leLeaseDeposit" />
      <el-table-column label="" align="center" prop="lePayType" />
      <el-table-column
        label=""
        align="center"
        prop="lelContractEnd"
        width="180"
      >   
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.lelContractEnd, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      
      <el-table-column
        label=""
        align="center"
        class-name="small-padding fixed-width "
        width="150px"
        fixed="right"     
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:lease:edit']"
            ></el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:lease:remove']"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <!-- <el-form-item label="Id" prop="sysHouseId">
          <el-input v-model="form.sysHouseId" placeholder="Id" />
        </el-form-item> -->
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="leLeaseUser">
              <el-input v-model="form.leLeaseUser" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="leBussLicense">
              <el-input
                v-model="form.leBussLicense"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="leLegalPerson">
              <el-input v-model="form.leLegalPerson" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="leLegalTell">
              <el-input
                v-model="form.leLegalTell"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="leCheckIndate">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.leCheckIndate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="leContractNearby">
              <el-input
                v-model="form.leContractNearby"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="leLeaseMonth">
              <el-input
                v-model="form.leLeaseMonth"
                placeholder=""
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="leLeaseDeposit">
              <el-input
                v-model="form.leLeaseDeposit"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="lePayType" >
              <el-select
                v-model="form.lePayType"
                placeholder=""
                style="width:100%"
              >
                <el-option label="" value="1" />
                <el-option label="" value="2" />
                <el-option label="" value="3" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="lelContractEnd">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.lelContractEnd"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listLease,
  getLease,
  delLease,
  addLease,
  updateLease,
  exportLease,
} from "@/api/buss/house/lease";

export default {
  name: "Lease",
  components: {},
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      leaseList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysHouseId: null,
        leLeaseUser: null,
        leBussLicense: null,
        leLegalPerson: null,
        leLegalTell: null,
        leCheckIndate: null,
        leContractNearby: null,
        leLeaseMonth: null,
        leLeaseDeposit: null,
        lePayType: null,
        lelContractEnd: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      },
      // 
      form: {},
      // 
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listLease(this.queryParams).then((response) => {
        this.leaseList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        sysHouseId: null,
        leLeaseUser: null,
        leBussLicense: null,
        leLegalPerson: null,
        leLegalTell: null,
        leCheckIndate: null,
        leContractNearby: null,
        leLeaseMonth: null,
        leLeaseDeposit: null,
        lePayType: null,
        lelContractEnd: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getLease(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateLease(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addLease(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '"' + ids + '"?',
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(function () {
          return delLease(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("");
        });
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(function () {
          return exportLease(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
