import request from '@/utils/request'

// 
export function listOnduty(query) {
  return request({
    url: '/buss/base/onduty/list',
    method: 'get',
    params: query
  })
}

// 
export function getOnduty(id) {
  return request({
    url: '/buss/base/onduty/' + id,
    method: 'get'
  })
}

// 
export function addOnduty(data) {
  return request({
    url: '/buss/base/onduty',
    method: 'post',
    data: data
  })
}

// 
export function updateOnduty(data) {
  return request({
    url: '/buss/base/onduty',
    method: 'put',
    data: data
  })
}

// 
export function delOnduty(id) {
  return request({
    url: '/buss/base/onduty/' + id,
    method: 'delete'
  })
}

// 
export function exportOnduty(query) {
  return request({
    url: '/buss/base/onduty/export',
    method: 'get',
    params: query
  })
}