import request from '@/utils/request'

// 
export function listPaper(query) {
  return request({
    url: '/buss/base/paper/list',
    method: 'get',
    params: query
  })
}

// 
export function getPaper(id) {
  return request({
    url: '/buss/base/paper/' + id,
    method: 'get'
  })
}

// 
export function addPaper(data) {
  return request({
    url: '/buss/base/paper',
    method: 'post',
    data: data
  })
}

// 
export function updatePaper(data) {
  return request({
    url: '/buss/base/paper',
    method: 'put',
    data: data
  })
}

// 
export function delPaper(id) {
  return request({
    url: '/buss/base/paper/' + id,
    method: 'delete'
  })
}

// 
export function exportPaper(query) {
  return request({
    url: '/buss/base/paper/export',
    method: 'get',
    params: query
  })
}