import request from '@/utils/request'

// 
export function listInspection(query) {
  return request({
    url: '/buss/device/inspection/list',
    method: 'get',
    params: query
  })
}

// 
export function getInspection(id) {
  return request({
    url: '/buss/device/inspection/' + id,
    method: 'get'
  })
}

// 
export function addInspection(data) {
  return request({
    url: '/buss/device/inspection',
    method: 'post',
    data: data
  })
}

// 
export function updateInspection(data) {
  return request({
    url: '/buss/device/inspection',
    method: 'put',
    data: data
  })
}

// 
export function delInspection(id) {
  return request({
    url: '/buss/device/inspection/' + id,
    method: 'delete'
  })
}

// 
export function exportInspection(query) {
  return request({
    url: '/buss/device/inspection/export',
    method: 'get',
    params: query
  })
}