import request from '@/utils/request'

// 
export function listInout(query) {
  return request({
    url: '/buss/safe/inout/list',
    method: 'get',
    params: query
  })
}

// 
export function getInout(id) {
  return request({
    url: '/buss/safe/inout/' + id,
    method: 'get'
  })
}

// 
export function addInout(data) {
  return request({
    url: '/buss/safe/inout',
    method: 'post',
    data: data
  })
}

// 
export function updateInout(data) {
  return request({
    url: '/buss/safe/inout',
    method: 'put',
    data: data
  })
}

// 
export function delInout(id) {
  return request({
    url: '/buss/safe/inout/' + id,
    method: 'delete'
  })
}

// 
export function exportInout(query) {
  return request({
    url: '/buss/safe/inout/export',
    method: 'get',
    params: query
  })
}