import request from '@/utils/request'

// 
export function listVedio(query) {
  return request({
    url: '/buss/safe/vedio/list',
    method: 'get',
    params: query
  })
}

// 
export function getVedio(id) {
  return request({
    url: '/buss/safe/vedio/' + id,
    method: 'get'
  })
}

// 
export function addVedio(data) {
  return request({
    url: '/buss/safe/vedio',
    method: 'post',
    data: data
  })
}

// 
export function updateVedio(data) {
  return request({
    url: '/buss/safe/vedio',
    method: 'put',
    data: data
  })
}

// 
export function delVedio(id) {
  return request({
    url: '/buss/safe/vedio/' + id,
    method: 'delete'
  })
}

// 
export function exportVedio(query) {
  return request({
    url: '/buss/safe/vedio/export',
    method: 'get',
    params: query
  })
}