import request from '@/utils/request'

// 
export function listAssets(query) {
  return request({
    url: '/buss/house/assets/list',
    method: 'get',
    params: query
  })
}

// 
export function getAssets(id) {
  return request({
    url: '/buss/house/assets/' + id,
    method: 'get'
  })
}

// 
export function addAssets(data) {
  return request({
    url: '/buss/house/assets',
    method: 'post',
    data: data
  })
}

// 
export function updateAssets(data) {
  return request({
    url: '/buss/house/assets',
    method: 'put',
    data: data
  })
}

// 
export function delAssets(id) {
  return request({
    url: '/buss/house/assets/' + id,
    method: 'delete'
  })
}

// 
export function exportAssets(query) {
  return request({
    url: '/buss/house/assets/export',
    method: 'get',
    params: query
  })
}