<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="" prop="cpFloor">
        <el-select
          v-model="queryParams.cpFloor"
          placeholder=""
          style="width: 100%"
        >
          <el-option label="" value="1" />
          <el-option label="" value="2" />
          <el-option label="" value="3" />
          <el-option label="" value="4" />
          <el-option label="" value="5" />
          <el-option label="" value="6" />
          <el-option label="" value="7" />
          <el-option label="" value="8" />
          <el-option label="" value="9" />
          <el-option label="" value="10" />
        </el-select>
        <!-- <el-input
          v-model="queryParams.cpFloor"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        /> -->
      </el-form-item>
      <el-form-item label="" prop="cpStatus">
        <el-select
          v-model="queryParams.cpStatus"
          placeholder=""
          clearable
          size="small"
        >
          <el-option label="" value="0" />
          <el-option label="" value="1" />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          ></el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:assets:add']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:assets:edit']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:assets:remove']"
          ></el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="assetsList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="cpFloor" />
      <el-table-column label="" align="center" prop="cpNumber" />
      <el-table-column label="" align="center" prop="cpType" />
      <el-table-column label="" align="center" prop="cpArea" />
      <el-table-column label="" align="center" prop="cpNature" />
      <el-table-column label="" align="center" prop="cpStatus" />
      <el-table-column label="" align="center" prop="cpContent" />
      <el-table-column label="" align="center" prop="cpPropertyOwner" />
      <el-table-column
        label=""
        align="center"
        prop="cpCertificateTitle"
      />
      <el-table-column label="" align="center" prop="cpImage" />
      <el-table-column
        label=""
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:assets:edit']"
            ></el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:assets:remove']"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="750px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="cpFloor">
              <el-select
                v-model="form.cpFloor"
                placeholder=""
                style="width: 100%"
              >
                <el-option label="" value="1" />
                <el-option label="" value="2" />
                <el-option label="" value="3" />
                <el-option label="" value="4" />
                <el-option label="" value="5" />
                <el-option label="" value="6" />
                <el-option label="" value="7" />
                <el-option label="" value="8" />
                <el-option label="" value="9" />
                <el-option label="" value="10" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="cpNumber">
              <el-input v-model="form.cpNumber" placeholder="" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="cpType">
              <el-select
                v-model="form.cpType"
                placeholder=""
                style="width: 100%"
              >
                <el-option label="" value="1" />
                <el-option label="" value="2" />
                <el-option label="" value="3" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="cpArea">
              <el-input v-model="form.cpArea" placeholder="" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="cpNature">
              <el-input v-model="form.cpNature" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="cpStatus">
              <el-select
                v-model="form.cpStatus"
                placeholder=""
                style="width: 100%"
              >
                <el-option label="" value="0" />
                <el-option label="" value="1" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="cpPropertyOwner">
              <el-input
                v-model="form.cpPropertyOwner"
                placeholder=""
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="cpCertificateTitle">
              <el-input
                v-model="form.cpCertificateTitle"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <uploadImage v-model="form.cpImage" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="cpUpdateDate">
              <el-date-picker
                clearable
                size="small"
                v-model="form.cpUpdateDate"
                type="date"
                style="width: 100%"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="">
              <el-input
                type="textarea"
                v-model="form.cpContent"
                :autosize="{ minRows: 5, maxRows: 10 }"
                maxlength="2000"
                show-word-limit
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listAssets,
  getAssets,
  delAssets,
  addAssets,
  updateAssets,
  exportAssets,
} from "@/api/buss/house/assets";
import UploadImage from "@/components/UploadImage";
import Editor from "@/components/Editor";

export default {
  name: "Assets",
  components: {
    UploadImage,
    Editor,
  },
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      assetsList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        cpNumber: null,
        cpFloor: null,
        cpType: null,
        cpArea: null,
        cpNature: null,
        cpStatus: null,
        cpPropertyOwner: null,
        cpCertificateTitle: null,
        cpUpdateDate: null,
        cpImage: null,
        cpContent: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      },
      // 
      form: {},
      // 
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listAssets(this.queryParams).then((response) => {
        this.assetsList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        cpNumber: null,
        cpFloor: null,
        cpType: null,
        cpArea: null,
        cpNature: null,
        cpStatus: null,
        cpPropertyOwner: null,
        cpCertificateTitle: null,
        cpUpdateDate: null,
        cpImage: null,
        cpContent: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getAssets(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateAssets(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addAssets(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '"' + ids + '"?',
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(function () {
          return delAssets(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("");
        });
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(function () {
          return exportAssets(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
