import request from '@/utils/request'

// 
export function listEvent(query) {
  return request({
    url: '/buss/emergency/event/list',
    method: 'get',
    params: query
  })
}

// 
export function getEvent(id) {
  return request({
    url: '/buss/emergency/event/' + id,
    method: 'get'
  })
}

// 
export function addEvent(data) {
  return request({
    url: '/buss/emergency/event',
    method: 'post',
    data: data
  })
}

// 
export function updateEvent(data) {
  return request({
    url: '/buss/emergency/event',
    method: 'put',
    data: data
  })
}

// 
export function delEvent(id) {
  return request({
    url: '/buss/emergency/event/' + id,
    method: 'delete'
  })
}

// 
export function exportEvent(query) {
  return request({
    url: '/buss/emergency/event/export',
    method: 'get',
    params: query
  })
}