import request from '@/utils/request'

// 
export function listWorks(query) {
  return request({
    url: '/buss/workshop/works/list',
    method: 'get',
    params: query
  })
}

// 
export function getWorks(id) {
  return request({
    url: '/buss/workshop/works/' + id,
    method: 'get'
  })
}

// 
export function addWorks(data) {
  return request({
    url: '/buss/workshop/works',
    method: 'post',
    data: data
  })
}

// 
export function updateWorks(data) {
  return request({
    url: '/buss/workshop/works',
    method: 'put',
    data: data
  })
}

// 
export function delWorks(id) {
  return request({
    url: '/buss/workshop/works/' + id,
    method: 'delete'
  })
}

// 
export function exportWorks(query) {
  return request({
    url: '/buss/workshop/works/export',
    method: 'get',
    params: query
  })
}