import request from '@/utils/request'

// 
export function listInvestigation(query) {
  return request({
    url: '/buss/workshop/investigation/list',
    method: 'get',
    params: query
  })
}

// 
export function getInvestigation(id) {
  return request({
    url: '/buss/workshop/investigation/' + id,
    method: 'get'
  })
}

// 
export function addInvestigation(data) {
  return request({
    url: '/buss/workshop/investigation',
    method: 'post',
    data: data
  })
}

// 
export function updateInvestigation(data) {
  return request({
    url: '/buss/workshop/investigation',
    method: 'put',
    data: data
  })
}

// 
export function delInvestigation(id) {
  return request({
    url: '/buss/workshop/investigation/' + id,
    method: 'delete'
  })
}

// 
export function exportInvestigation(query) {
  return request({
    url: '/buss/workshop/investigation/export',
    method: 'get',
    params: query
  })
}