import request from '@/utils/request'

// 
export function listRental(query) {
  return request({
    url: '/buss/workshop/rental/list',
    method: 'get',
    params: query
  })
}

// 
export function getRental(id) {
  return request({
    url: '/buss/workshop/rental/' + id,
    method: 'get'
  })
}

// 
export function addRental(data) {
  return request({
    url: '/buss/workshop/rental',
    method: 'post',
    data: data
  })
}

// 
export function updateRental(data) {
  return request({
    url: '/buss/workshop/rental',
    method: 'put',
    data: data
  })
}

// 
export function delRental(id) {
  return request({
    url: '/buss/workshop/rental/' + id,
    method: 'delete'
  })
}

// 
export function exportRental(query) {
  return request({
    url: '/buss/workshop/rental/export',
    method: 'get',
    params: query
  })
}