<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="" prop="sysHouseId">
        <el-input
          v-model="queryParams.sysHouseId"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="sysHouseId">
        <el-select v-model="form.lePayType" placeholder="">
          <el-option label="" value="1" />
          <el-option label="" value="2" />
          <el-option label="" value="3" />
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="rlContractEnd">
        <el-date-picker
          clearable
          size="small"
          style="width: 200px"
          v-model="queryParams.rlContractEnd"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          ></el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:other:add']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:other:edit']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:other:remove']"
          ></el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>
    <el-table
      v-loading="loading"
      :data="otherList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="thPayContent" />
      <el-table-column label="" align="center" prop="thPayAmount" />
      <el-table-column label="" align="center" prop="rlPayType" />
      <el-table-column
        label=""
        align="center"
        prop="rlPayDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlPayDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:other:edit']"
            ></el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:other:remove']"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
           <el-form-item label="" prop="thPayAmount">
              <el-input
                v-model="form.thPayAmount"
                placeholder=""
              />
            </el-form-item> 
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="rlPayDate">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.rlPayDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="rlPayType">
              <el-select v-model="form.rlPayType" placeholder="" style="width: 100%">
                <el-option label="" value="" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="rlContractEnd">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.rlContractEnd"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">           
            <el-form-item label="">
               <el-input
                type="textarea"
                v-model="form.thPayContent"
                :autosize="{ minRows: 5, maxRows: 10 }"
                maxlength="2000"
                show-word-limit
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listOther,
  getOther,
  delOther,
  addOther,
  updateOther,
  exportOther,
} from "@/api/buss/house/other";
import Editor from "@/components/Editor";

export default {
  name: "Other",
  components: {
    Editor,
  },
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      otherList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysHouseId: null,
        sysLeaseId: null,
        thPayContent: null,
        thPayAmount: null,
        rlPayType: null,
        rlPayDate: null,
        rlContractEnd: null,
      },
      // 
      form: {},
      // 
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listOther(this.queryParams).then((response) => {
        this.otherList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        sysHouseId: null,
        sysLeaseId: null,
        thPayContent: null,
        thPayAmount: null,
        rlPayType: null,
        rlPayDate: null,
        rlContractEnd: null,
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getOther(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateOther(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addOther(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '"' + ids + '"?',
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(function () {
          return delOther(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("");
        });
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(function () {
          return exportOther(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
