<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="" prop="sysHouseId">
        <el-input
          v-model="queryParams.sysHouseId"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="sysHouseId">
        <el-select v-model="form.lePayType" placeholder="">
          <el-option label="" value="1" />
          <el-option label="" value="2" />
          <el-option label="" value="3" />
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="elContractEnd">
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.elContractStart"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
        >
        </el-date-picker
        >-
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.elContractEnd"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          ></el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:water:add']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:water:edit']"
          ></el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:water:remove']"
          ></el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="waterList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="" align="center" prop="elTonCurrrent" />
      <el-table-column label="" align="center" prop="elTonPre" />
      <el-table-column
        label=""
        align="center"
        prop="elTonDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elTonDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="elTonSum" />
      <el-table-column label="" align="center" prop="elTonPrice" />
      <el-table-column label="" align="center" prop="elPayShould" />
      <el-table-column label="" align="center" prop="elPayReality" />
      <el-table-column
        label=""
        align="center"
        prop="elPayDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elPayDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="elPayType" />
      <el-table-column
        label=""
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:water:edit']"
            ></el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:water:remove']"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elTonPre">
              <el-input v-model="form.elTonPre" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elTonCurrrent">
              <el-input
                v-model="form.elTonCurrrent"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elTonDate">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.elTonDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elTonSum">
              <el-input v-model="form.elTonSum" placeholder="" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elTonPrice">
              <el-input
                v-model="form.elTonPrice"
                placeholder=""
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elPayShould">
              <el-input
                v-model="form.elPayShould"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elPayReality">
              <el-input
                v-model="form.elPayReality"
                placeholder=""
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elPayDate">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.elPayDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="elPayType">
              <el-select v-model="form.elPayType" placeholder="" style="width: 100%">
                <el-option label="" value="1" />
                <el-option label="" value="2" />
                <el-option label="" value="3" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="elContractEnd">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.elContractEnd"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder=""
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listWater,
  getWater,
  delWater,
  addWater,
  updateWater,
  exportWater,
} from "@/api/buss/house/water";

export default {
  name: "Water",
  components: {},
  data() {
    return {
      // 
      loading: true,
      // 
      ids: [],
      // 
      single: true,
      // 
      multiple: true,
      // 
      showSearch: true,
      // 
      total: 0,
      // 
      waterList: [],
      // 
      title: "",
      // 
      open: false,
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysHouseId: null,
        sysLeaseId: null,
        elTonCurrrent: null,
        elTonPre: null,
        elTonDate: null,
        elTonSum: null,
        elTonPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      },
      // 
      form: {},
      // 
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /**  */
    getList() {
      this.loading = true;
      listWater(this.queryParams).then((response) => {
        this.waterList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 
    cancel() {
      this.open = false;
      this.reset();
    },
    // 
    reset() {
      this.form = {
        id: null,
        sysHouseId: null,
        sysLeaseId: null,
        elTonCurrrent: null,
        elTonPre: null,
        elTonDate: null,
        elTonSum: null,
        elTonPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      };
      this.resetForm("form");
    },
    /**  */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /**  */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /**  */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "";
    },
    /**  */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getWater(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "";
      });
    },
    /**  */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateWater(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          } else {
            addWater(this.form).then((response) => {
              this.msgSuccess("");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /**  */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '"' + ids + '"?',
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(function () {
          return delWater(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("");
        });
    },
    /**  */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(function () {
          return exportWater(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
