import request from '@/utils/request'

// 
export function listTrack(query) {
  return request({
    url: '/buss/emergency/track/list',
    method: 'get',
    params: query
  })
}

// 
export function getTrack(id) {
  return request({
    url: '/buss/emergency/track/' + id,
    method: 'get'
  })
}

// 
export function addTrack(data) {
  return request({
    url: '/buss/emergency/track',
    method: 'post',
    data: data
  })
}

// 
export function updateTrack(data) {
  return request({
    url: '/buss/emergency/track',
    method: 'put',
    data: data
  })
}

// 
export function delTrack(id) {
  return request({
    url: '/buss/emergency/track/' + id,
    method: 'delete'
  })
}

// 
export function exportTrack(query) {
  return request({
    url: '/track/export',
    method: 'get',
    params: query
  })
}